export default {
	data() {
		return {
			value: ''
		}
	},
	onLoad() {
	},
	onShareAppMessage(res) {
		const title = ['孩子名字写的歪歪扭扭，熊猫老师教你名字变好看！', '孩子名字写的歪歪扭扭， 专业名师免费教你写名字!']
		return {
			title: title[Math.floor(Math.random() + 0.5)],
			path: '/pages/index/index',
			imageUrl: `../../static/img/invite.png`,
			success: function(res) {
				console.log("转发回调", res)
			},
			fail: function(res) {
				console.log("失败")
			}
		}
	},
	methods: {
		linkHandle () {
			if (this.value.length < 2) {
				uni.showToast({
					title: '请输入2个以上的汉字',
					icon: 'none',
					mask: true
				})
				return
			}
			if (this.value.length > 6) {
				uni.showToast({
					title: '请输入2个以上的汉字',
					icon: 'none',
					mask: true
				})
				return
			}
			let reg= /^[\u4e00-\u9fa5]+$/
			if (!reg.test(this.value)){
				uni.showToast({
					title: '请输入2个以上的汉字',
					icon: 'none',
					mask: true
				})
				return false
			}
			uni.navigateTo({
				url:  `/pages/detail/detail?name=${this.value}`
			})
		}
	},
	computed: {
		navigationBarHeight () {
			return parseInt(this.$store.state.systeminfo.statusBarHeight)
		},
		src () {
			return this.$store.state.imgsrc
		}
	}
}
